../base/Table.vue<template>
  <div>                
    <CRow>
      <CCol lg="12">
        <pulse-loader class="spinner" v-if="stockLoading" :loading="stockLoading"></pulse-loader>
        <CTableWrapper small="false" v-on:action="actionTriggered" v-if="!stockLoading" :fields="['Familia', 'Quantity']" :items="stocks" >          
          <template #header>
            <CIcon name="cil-grid"/> Estoque
            <div class="card-header-actions">
              <a 
                href="javascript:void(0)" 
                v-on:click="load()"
              >
                <CBadge color="secondary">LOAD</CBadge>
              </a>
            </div>
          </template>
        </CTableWrapper>        
      </CCol>            
    </CRow>
  </div>
</template>

<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
import CTableWrapper from '../base/Table.vue'

export default {
  name: 'EstoquePending',
  components: { CTableWrapper, PulseLoader },
  computed: {
    stockLoading() { return !this.$store.state.stock.stockLoaded},
    stocks() { return this.$store.state.stock.stocks }
  },
  mounted: function() {
    if(this.$store.state.stock.stocks.length === 0){
      this.load();
    }
  },
  methods: {        
    load(){
      this.$store.dispatch('stock/fetchStocks');    
    },   
  }
}
</script>
<style>
.spinner {
  display: block;
  position: fixed;
  z-index: 1031; /* High z-index so it is on top of the page */
  top: 50%;
  right: 50%; /* or: left: 50%; */
  margin-top: -..px; /* half of the elements height */
  margin-right: -..px; /* half of the elements widht */
}
</style>